export default class about extends React.Component {
  componentWillMount() {}
  componentDidMount() {
    refreshall();
    document.title = "React router App-" + "about";
  }
  componentWillReceiveProps(newProps) {}
  shouldComponentUpdate(newProps, newState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {}
  componentDidUpdate(prevProps, prevState) {}
  componentWillUnmount() {}
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img
            src="data:image/svg+xml;base64,"
            className="App-logo"
            alt="logo"
          />
          <p>
            <a
              target="_blank"
              href="https://zh-hans.reactjs.org/tutorial/tutorial.html"
            >
              入门教程: 认识 React – React
            </a>
          </p>
          <p>
            <a
              target="_blank"
              href="https://zh-hans.reactjs.org/docs/getting-started.html"
            >
              开始 – React文档
            </a>
          </p>
          <h1>react-router测试</h1>
          <h2>现在是 {new Date().toLocaleTimeString()}.</h2>
          <h3>
            React 是一个用于构建用户界面的 JAVASCRIPT 库。
            <br />
            React主要用于构建UI，很多人认为 React 是 MVC 中的 V（视图）。
            <br />
            React 起源于 Facebook 的内部项目，用来架设 Instagram 的网站，并于
            2013 年 5 月开源。
            <br />
            React
            拥有较高的性能，代码逻辑非常简单，越来越多的人已开始关注和使用它。
            <br />
            React 特点
            <br />
            1.声明式设计 −React采用声明范式，可以轻松描述应用。
            <br />
            2.高效 −React通过对DOM的模拟，最大限度地减少与DOM的交互。
            <br />
            3.灵活 −React可以与已知的库或框架很好地配合。
            <br />
            4.JSX − JSX 是 JavaScript 语法的扩展。React 开发不一定使用 JSX
            ，但我们建议使用它。
            <br />
            5.组件 − 通过 React
            构建组件，使得代码更加容易得到复用，能够很好的应用在大项目的开发中。
            <br />
            6.单向响应的数据流 − React
            实现了单向响应的数据流，从而减少了重复代码，这也是它为什么比传统数据绑定更简单。
          </h3>
          <div>
            <p>
              Edit <code>src/App.js</code> and save to reload.
            </p>
            <a
              className="App-link"
              href="https://reactjs.org"
              target="_blank"
              rel="noopener noreferrer"
            >
              Learn React
            </a>
          </div>
        </header>
      </div>
    );
  }
}
function refreshall() {
  $("#allnavbar").click();
}
